@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Tajawal:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600;700;800&display=swap);
.Homepage {
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
.h-left {
  position: absolute;
  top: 30vh;
  font-family: "Roboto", sans-serif;
  left: 25px;
  line-height: 1.3;
}
.h-left h1 {
  font-weight: 500;
  font-size: 4.2rem;
}
.h-left h2 {
  font-weight: 300;
  font-size: 3.5rem;
}
.h-left button {
  margin-top: 8px;
  font-weight: 300;
  padding: 12px 18px;
  background-color: white;
  color: black;
}
.h-left button{
  margin-right: 30px;
}

.h-left .main button{
  background-image: linear-gradient( 102.4deg,  rgba(253,189,85,1) 7.8%, rgba(249,131,255,1) 100.3% );
  color: white;
  margin-bottom: 20px;
}

body {
  background-color: rgb(219, 218, 218);
  padding: 0 0rem 0 25rem;
}

#heading {
  text-align: center;
  margin-bottom: 30px;
}
*:focus {
  outline: none;
}
h1,
h2,
h3,
h4 {
  margin: 10px 0;
  font-weight: 400;
}
h3 {
  font-size: 1.22rem;
}
h4 {
  font-size: 1.2rem;
}
.pad p {
  margin: 10px 0;
  font-weight: 300;
  letter-spacing: 0.02cm;
  font-size: 1.1rem;
}
.cur {
  font-style: italic;
}
ul,
ol {
  margin: 10px 0;
}
hr {
  margin-left: 0;
  width: 96%;
  opacity: 0.5;
}
canvas {
  transition: 0.3s;
  border: 1px solid black;
  width: 100%;
}
.pad input {
  background-color: transparent;
  border: none;
  font-size: 2.5rem;
  font-weight: 300;
}
.Whole {
  
  background-color: white;
  overflow: scroll;
  position: relative;
  height: 50rem;
  width: 55rem;
  /* padding: 5px 10px 30px 10px; */
  box-shadow: -2px -2px 15px 1px #0000000d;
  cursor: url(/Type-writer/static/media/Vector.8183f6a0.svg), auto;  
  z-index:0;
}

p,
h1,
h2,
h3,
h4,
li {
  cursor: url(/Type-writer/static/media/Group.ff176278.svg), auto;
}
.outline{
  outline: solid;
  outline-offset:-10px; 
  outline-width:1px;
}
.paper{
  background-image: url(/Type-writer/static/media/paper-like.fe036721.jpeg);
}

.codes {
  background-color: rgb(238, 232, 232);
  width: 600px;
  border-radius: 6px;
  padding: 10px 10px;
  margin: 20px 20px 20px 30px;
  font-family: 'Ubuntu Mono', monospace;
  font-size: 1.2rem;
  position: relative;
  height: 15px;
}

.box{
    border: 2px solid black;
    cursor:se-resize;
}
.panel {
  width: 16rem;
  height: 100vh;
  position: fixed;
  border: 1.5px solid rgb(207, 206, 206);
  left: 0;
  top: 0vh;
  font-family: "Roboto", sans-serif;
  padding: 0 10px;
  background-color: white;
  overflow: scroll;
}
.drop {
  margin-top: 50px;
  font-weight: 300;
}



.cheader {
  margin-top: 30px;
  border-bottom: 1px solid rgb(235, 228, 228);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 10px;
  margin-bottom: 30px;
  position: relative;
}
.cheader p {
  font-weight: 300;
  font-size: 1.4rem;
  display: inline-block;
}
p {
  margin: 0;
}
.cheader button {
  display: inline-block;
  position: absolute;
  right: 0;
  bottom: 3%;
  
}
button{
  background-color: black;
  color: white;
  font-size: 1.25rem;
  margin: 0;
  padding: 9px;
  border-radius: 7px;
  border: none;
  cursor: pointer;
}

.version-btn{
  background-image: linear-gradient( 102.4deg,  rgba(253,189,85,1) 7.8%, rgba(249,131,255,1) 100.3% );
  color: white;
  border: none;
}

.saved-versions {
text-align: center;
}
.versions {
  margin: 40px 40px;
}





.User-page {
  width: 100%;
  height: 100vh;
  position: fixed;
  left: 0;
  top: 0;
}
.form .input {
  display: block;
  margin: 10px 0;
}
.form {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 40vh 0 0 10vh;
}
.form button {
  margin-right: 20px;
  margin-top: 10px;
  width: 50%;
}

